body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: gray;
}

a{
  text-decoration: none;
}

.quote{
  display: block;
  color:  #00FF00;
}

.spoiler, .spoiler > * {
  -webkit-transition: color 0.5s, opacity 0.5s;
  -o-transition: color 0.5s, opacity 0.5s;
  transition: color 0.5s, opacity 0.5s
}
.spoiler:hover {
  color: inherit;
  background-color: inherit;
}
.spoiler:hover > *
{
  opacity: 0
}
.spoiler, .spoiler > *:not(a) {
  color: black;
  background-color: black;
}

.app {
  text-align: center;
}

.appHeader {
  background-color: #222;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 20px;
  color: white;
}

.appTitle {
  font-size: 1.5em;
}

a, a:hover, a:focus {
  text-decoration: none;
}


a:link {
  color: inherit;
}
a:visited {
  color: inherit;
}

a.postRef {
  color: cadetblue;
}

pre {
  overflow: auto;
  max-width: 100%;
}

.alert {
  width: 95%;
  border-radius: 10px;
  margin-top: 10px;
  padding: 5px;
  margin-left: auto;
  margin-right: auto;
}

.alert-info{
  background-color: #FCE95F;
}
.alert-error{
  background-color: #e74c3c;
  color: whitesmoke;
}

@media screen and (max-width: 500px) {

  #smallDiv{
    -ms-flex-item-align: center;
        align-self: center;
    text-align: center;
    margin: auto;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    visibility: visible;
  }
}



