#fileSelect {
  margin-right: 20px;
  width: 200px;
  height: 200px;
  text-align:center;
  -webkit-transition: background-color 1s;
  -o-transition: background-color 1s;
  transition: background-color 1s;
}


#fileElem{
    display: none;
}

#fileSelect > img {
  max-width:100%;
  max-height:100%;
}

#uploadImage{
  height: 100%;
  width: 100%;
}



